import React from 'react';
import { Typography,Button} from '@mui/material'
const MAIL = 'mailto:manvirkaur094@gmail.com'
  
function Contact (){
    return (
    <div className='app-container contact'>
        <Typography variant="h4">Get In Touch</Typography>  
        <Typography variant='h5'>
        If you have any questions, comments, or feedback, please feel free to reach out to me.
        I'm always happy to hear from you and respond to your inquiries.
        You can reach me by emailing me through the following button.
        <br></br>
        <br></br>
        <a href={MAIL} target="_blank" rel="noreferrer" className='btn-link'><Button variant="outlined">Let's talk</Button></a>
        <br></br>
        <br></br>
        I look forward to hearing from you!
        </Typography>
    </div>
    )
}
  
export default Contact;