import React from 'react';
import { Typography} from '@mui/material';
import { Grid } from '@mui/material';
import profile from '../images/profile-photo.png';
  
function About (){
    return (
        <div className='app-container'>
            <Grid container spacing={2}>
            <Grid sm={4} xs={12} className='profile-container'>
                <img src={profile}  alt="profile" className='profile-img'/>
            </Grid>

            <Grid sm={8} xs={12}>
                <Typography variant="h4">About me</Typography>
                <Typography variant="subtitle1">
                    Hello, my name is Manvir and I am a Software enthusiast.
                    I have a Bachelor's degree in Computer Science and Engineering
                    along with software web development experience of about 3 years.
                    I strive to provide efficient solutions to the clients while collaborating 
                    with the team using the agile principles.
                    Motivation ,teamwork , optimism and zeal to learn are the key principles
                    that push me to face the challenges
                    of the advancements in technology.
                    <br></br>
                    <br></br>
                    Currently, being an AI student, I have been able to
                    continuously gain knowledge in this domain through my program and built 
                    Machine learning models.I am looking forward for an opportunity to step
                    into the software development market and develop my career as a Software professional.
                    <br></br>
                    <br></br>
                    Here are a few technologies I have worked with:
                    <ul className='tech-list'>
                        <li>HTML</li>
                        <li>CSS</li>
                        <li>Javascript</li>
                        <li>React</li>
                        <li>Typescript</li>
                        <li>Nodejs</li>
                        <li>Expressjs</li>
                        <li>Python</li>
                    </ul>
                </Typography>
            </Grid>
            </Grid>
        </div>
    )
}
  
export default About;