import React from 'react';
import { Typography,Button} from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import { Grid } from '@mui/material';
import Resume from "../Resume.pdf";

const GITHUB_LINK = 'https://github.com/mkaur791'
const LINKEDIN = 'https://www.linkedin.com/in/manvir-kaur-93979a112/'
const MAIL = 'mailto:manvirkaur094@gmail.com'

function Home (){
    return(
        <div className='app-container'>
            <Grid container spacing={2}>
            <Grid sm={8} xs={12}>
                <Typography variant="h6">Welcome to my porfolio site. Nice to see you here !</Typography>
                <Typography variant="h2">I am Manvir Kaur. </Typography>
                <Typography variant="h3" gutterBottom>
                    I bring ideas to life with code.
                </Typography>
            </Grid>

            <Grid sm={4} xs={12}>
                <Typography variant="subtitle1">I am a Software Engineering Technology and Artificial Intelligence 
                    student who is passionate about building softwares that solve real-word problems.
                    I have strong foundation in programming skills including  HTML, CSS , Javascript,
                    React as well as Python and Express js. My current area of exploration
                    is AI.
                </Typography>
                <a href={Resume} target="_blank" rel="noreferrer" className='btn-link'><Button variant="outlined">Here is my resume</Button></a>
                <div className='social-icon-container'>
                    <a href={GITHUB_LINK} target='_blank' rel="noreferrer"><GitHubIcon fontSize='large'/></a>
                    <a href={LINKEDIN} target='_blank' rel="noreferrer"><LinkedInIcon fontSize='large'/></a>
                    <a href={MAIL}><MailOutlineIcon fontSize='large'/></a>    
                 </div>  
            </Grid>
            </Grid>
        </div>
         )
}
  
export default Home;