import React, { Fragment } from 'react';
import { Typography,Grid, Paper} from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import pleaseLetUsKnow from '../images/please-let-us-know.png';
import supervisedML from '../images/supervised-ml.png';
import favouriteBookList from '../images/favourite-book-list.png'

const projects = [
    {   name: 'Please Let Us Know' , 
        image:pleaseLetUsKnow,
        text:'A basic survey site where a user can signup and create, edit and delete surveys. The website also provides the statistics for responses to the surveys',
        technologies: ['Nodejs', 'Expressjs' ,'Mongodb', 'ejs Templates'],
        githubUrl:'https://github.com/mkaur791/COMP229-F2021-TECHARMY'
    },
    {   name: 'Supervised ML modelling' , 
        image:supervisedML,
        text:'Project with ML models trained and deployed at backend. The frontend included making predictions and displaying the predicted results. It also included displaying the accuracy of each model.',
        technologies:['React','Python','CSS','Material UI'],
        githubUrl:'https://github.com/mkaur791/COMP247-group4-project-frontend'
    },
    {   name: 'Favourite Book List' ,
        image:favouriteBookList,
        text:'A basic CRUD application displaying the favourite books.',
        technologies:['Nodejs','Expressjs','Mongodb','ejs Templates'],
        githubUrl:'https://github.com/mkaur791/COMP229-F2021-Midterm-301171525'
    }
]
  
function Work (){
    return (
        <div className='app-container work'>
        <Typography variant="h4">Getting Things Done: My Work Portfolio</Typography>
        <Typography variant="h5">Here are a few of my academic projects:</Typography>
        <br></br>
        {projects.map((project, index) => (
            <Fragment key={index}>
            <Grid container>
                <Grid sm={6} xs={12}>
                        {index % 2 === 0 ?
                        <img src={project.image}  alt={project.name}/>
                        :
                        <Paper elevation={3}>
                            <div className='project-title-container'>
                                <a href={project.githubUrl} target='_blank' rel="noreferrer"><GitHubIcon fontSize='small'/></a>
                                <Typography variant="h5">{project.name}</Typography>
                            </div>
                            <Typography variant="subtitle1">{project.text}</Typography>
                            <ul className='tech-list'>
                                {project.technologies.map((tech, ind) => (
                                    <li key={index+ind}>
                                        {tech}
                                    </li>
                                ))}      
                            </ul>
                        </Paper>}

                </Grid>
                <Grid sm={6} xs={12}>
                    {index % 2 === 0 ?
                    <Paper elevation={3}>
                        <div className='project-title-container'>
                            <a href={project.githubUrl} target='_blank' rel="noreferrer"><GitHubIcon fontSize='small'/></a>
                            <Typography variant="h5">{project.name}</Typography>
                        </div>
                        <Typography variant="subtitle1">{project.text}</Typography>
                        <ul className='tech-list'>
                            {project.technologies.map((tech, ind) => (
                                <li key={index+ind}>
                                   {tech}
                                </li>
                            ))}      
                        </ul>
                    </Paper>
                    :
                    <img src={project.image}  alt={project.name}/>}
                </Grid>
            </Grid>
            <hr></hr>
            </Fragment>
        ))}      
    </div>
    )
}
  
export default Work;